.DestinationPage {
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    background-image: url('../images/background-destination-desktop.jpg');
    background-size: cover;
}
  
.main-destination {
    height: 85%;
    width: 75%;
    display: flex;
    flex-direction: row;
    align-items: stretch;
    justify-content: stretch;
}

.left-side, .right-side {
    height: 100%;
    width: 50%;
}

.right-side {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}

.text-container {
    height: 72%;
    width: 80%;
    margin-top: 3%;
    margin-left: 5%;
}

.destination-picker-box {
    height: 10%;
}

.destination-picker-list {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    list-style: none;
    padding: 0;
    margin: 0;
    gap: 10%;
}

.destination-picker-item {
    height: 100%;
    color: rgb(211, 211, 211);
    text-decoration: none;
    letter-spacing: 2.7px;
    font-size: 1rem;
    font-family: 'Barlow Condensed', sans-serif;
    font-weight: 400;
    display: flex;
    align-items: center;
    justify-content: center;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.destination-picker-item:hover {
    padding-top: 3px;
    border-bottom: 3px solid rgb(211, 211, 211);
    cursor: pointer;
}

@media screen and (max-width: 480px) {

    .DestinationPage {
        height: auto;
        background-image: url('../images/background-destination-mobile.jpg');
        padding-bottom: 20%;
    }

    .main-destination {
        height: 85%;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: stretch;
        justify-content: flex-start;
        padding: 0 10%;
    }
    
    .left-side {
        height: 30%;
        width: 100%;
    }

    .right-side {
        height: 70%;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .text-container {
        height: 72%;
        width: 100%;
        margin-top: 3%;
        margin-left: 0;
    }

    .destination-picker-box {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    
    .destination-picker-list {
        align-items: center;
        justify-content: center;
    }

    .destination-picker-item:hover {
        padding-top: 0;
        border: none;
        cursor: pointer;
    }
}